body {
    // position: relative;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    justify-content: space-between;
    padding: 0;
    height: 100%;
    font-family: 'Panton-Regular';
  }
  .page-container {
    max-width: 100%;
    min-height: 100vh;
    height: auto;
    flex: 1;
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 50px 45px;
}

.title-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    .label-title{
        text-transform: uppercase;
        font-size: 16px;
        color: #685D5B;
       
    }
    .title{
        font-family: 'Panton-Bold';
        text-transform: inherit;
        font-size: 31px;
        color: #685D5B;
       
    }
}

.title-container--light{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    .label-title{
        text-transform: uppercase;
        font-size: 16px;
        color: white;
        font-family: 'Panton-Light';
       
    }
    .title{
        text-transform: uppercase;
        font-size: 31px;
        color: white;
       
    }
}

.bg-lighter{
    background-color: #fff;
    background-size: cover;
}
.bg-header--3{
    background:linear-gradient(0deg, rgba(20, 11, 16, 0.5), rgba(202, 192, 203, 0.5)), url("../../images/bg-3.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    
}
.bg-header--2{
    background:linear-gradient(0deg, rgba(20, 11, 16, 0.5), rgba(202, 192, 203, 0.5)), url("../../images/bg-section-2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    
}
.bg-dark{
    background-color: #22201F;
    background-size: cover;
}

.content-block{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;
    margin-top: 20px;
    font-size: 16px;
    color: #685D5B;
    
    margin-bottom: 65px;
    @media screen and (max-width:575.98px){
        flex-wrap: wrap;
    }
    @media screen and (min-width:576px)and(max-width:768px){
        flex-wrap: wrap;
    }
}

.content-block--2{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 66%;
    margin-left: auto;
    margin-top: 20px;
    margin-bottom: 0px;
    font-size: 16px;
    color: white;
    @media screen and (max-width:575.98px){
        width: 100%;
        flex-wrap: wrap;
        margin-left: 0px;
        margin-top: 0px;
        font-size: 10px;
        margin-bottom: 20px;
    }
    @media screen and (min-width:576px)and(max-width:768px){
        width: 100%;
        flex-wrap: wrap;
        margin-left: 0px;
    }
}

.content-block--3{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;
    font-size: 16px;
    color: white;
    font-family: 'Panton-Light';
    margin-bottom: 65px;
    
    @media screen and (max-width:575.98px){
        font-size: 14px;
    }
    @media screen and (min-width:576px)and(max-width:768px){
        width: 100%;
    }
}

.text-block--3{
    width: 80%;
    color: white;
    font-size: 16px;
    font-family: 'Panton-Light';
    padding-left: 18px;
}
.content-bottom{
    display: flex;
    flex-direction: column;
    margin-top: auto;
}
.list-team{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 5px;
    .container-team{
        display: flex;
        flex-direction: column;
        max-width: 200px;
        .avatar{
            width: 200px;
            height: auto;
        }
        .avatar-info{
            font-size: 16px;
            color: white;
            font-family: 'Panton-Light';
            margin-top: 20px;
        }
    }
}
.label-title{
    font-family: 'Panton-Bold';
    text-transform: inherit;
}
